class Hangman {
    constructor(word, remainingGuesses) {
        this.status = 'playing'
        this.word = word.toLowerCase().split('')
        this.guessedLetters = []
        this.remainingGuesses = remainingGuesses
    }

    get puzzle() {
        let puzzle = ''
        this.word.forEach(letter => {
            if (letter === ' ') {
                puzzle += ' '
            } else if (this.guessedLetters.includes(letter)) {
                puzzle += letter
            } else {
                puzzle += '*'
            }
        })
        return puzzle
    }

    makeGuess(character) {
        // Failed hay Finished ri th dng
        if (this.status !== 'playing') return;

        const char = character.toLowerCase()

        // Nu on ch  on ri th dng
        if (this.guessedLetters.includes(char)) return;
        
        // Cp nht nhng ch ci ngi chi  on
        // this.guessedLetters.push(char) // Cch 1
        this.guessedLetters = [...this.guessedLetters, char] // Cch 2
        
        // Nu ch ci ng chi on ko nm trong ch th gim lt on
        if ( ! this.word.includes(char)) 
            this.remainingGuesses--

        this.recalculatingStatus()
    }

    recalculatingStatus() {
        if (this.remainingGuesses === 0) {
            this.status = 'failed'
            return;
        }
    
        /**
         * * Cch 3: Dng every
         * - Nu tt c match tr v true
         * - C bt k ci no ko match tr v false
         */
        const isFinished = this.word.every(letter => this.guessedLetters.includes(letter) || letter === ' ')
        if (isFinished) {
            this.status = 'finished'
            return;
        }
    
        // Cha failed m cng cha finished th l ang chi
        this.status = 'playing'
    }

    get statusMessage() {
        if (this.status === 'playing') 
            return `Guesses left: ${this.remainingGuesses}.`
        else if (this.status === 'failed')
            return `Nice try! The word was "${this.word.join('')}".`
        
        return 'Great work! You guessed the word.'
    }
}

export { Hangman as default }





